import React, { useState } from 'react'
import { Dimmer, Loader, Label, Visibility, VisibilityEventData, StrictVisibilityProps } from 'semantic-ui-react'
import { FixedSizeList as List, ListChildComponentProps } from 'react-window';


import ListElement from './ListElement'
import { lineNames } from './fetchList';
type Calculations = {
    direction: string,
    height: number,
    width: number,
    topPassed: boolean,
    bottomPassed: boolean,
    pixelsPassed: number,
    percentagePassed: number,
    topVisible: boolean,
    bottomVisible: boolean,
    fits: boolean,
    passing: boolean,
    onScreen: boolean,
    offScreen: boolean
}

type handleUpdate = {
    e: any,
    calculations: Calculations

}

interface StopInterface {

}


const Row = ({ index, style, data }: ListChildComponentProps) => {
    return <div key={data.stops[index].stopId}
        style={{ overflow: 'hidden', borderBottom: '1px solid lightgrey', ...style }}
        onClick={() => data.manageActive(data.stops[index])}
    >
        <Label size='tiny' color={data.stops[index].operator === 'ztm' ? 'red' : 'blue'} content={data.stops[index].operator.toUpperCase()} />
        {data.stops[index].stopName + ' ' || data.stops[index].stopDesc + ' '}
        {data.stops[index].stopCode && data.stops[index].stopCode}
        {data.stops[index].distance && <Label circular color='olive' size='tiny' content={`${data.stops[index].distance >= 1000 ? (Math.round(data.stops[index].distance / 100)) / 10 + 'km' : data.stops[index].distance + 'm'}`} />}
        {data.lines && data.lines[data.stops[index].stopId]
            ? <>Linie: {data.lines[data.stops[index].stopId].map((line: any) => <Label key={line} circular size='tiny' content={lineNames(line)} />)} </>
            : null}
    </div>

}

const StopList = (props: any) => {
    const { stops, manageActive, activeIndex, lines, height }: any = props
    const [visibility, setVisibility] = useState<any>(450)

    const handleUpdate = (e: any, { calculations }: any): void => setVisibility(calculations)

    if (!stops) {
        return <Dimmer active inverted>
            <Loader inverted content='Pobieranie listy przystanków' />
        </Dimmer>

    }
    return <Visibility onUpdate={handleUpdate}>
        <List
            height={height}
            itemCount={stops.length}
            itemSize={45}
            width={visibility.width}
            itemData={{
                stops,
                manageActive,
                lines
            }}
        >
            {Row}
        </List>
    </Visibility>

}

export { StopList }